import React, { useState, useEffect } from "react";
import TodoService from "../Services/TodoService";

const Admin = () => {
  const [usuarios, setUsuarios] = useState([]);

  useEffect(() => {
    TodoService.getUsers().then((data) => {
      setUsuarios(data);
    });
  }, [usuarios]);

  const onDeletetHandler = (id) => {
    TodoService.deleteUser(id);
  };

  return (
    <div className="container">
      <h1>Admin Page</h1>
      <h2>Usuarios del sistema</h2>
      <ol className="list-group">
        {usuarios.map((usuario) => {
          return (
            <li className="alert alert-primary " key={usuario._id}>
              <b>Username: </b>
              {usuario.username}--<b> Role: </b>
              {usuario.role}---
              <button
                className="btn btn-danger"
                onClick={() => {
                  if (
                    window.confirm(
                      `Seguro que desea eliminar a ${usuario.username}??`
                    )
                  ) {
                    onDeletetHandler(usuario._id);
                  }
                }}
              >
                X
              </button>
            </li>
          );
        })}
      </ol>
    </div>
  );
};

export default Admin;
