import React, { useContext } from "react";
import { AuthContext } from "../Context/AuthContext";

const Home = () => {
  const { user, isAuthenticated } = useContext(AuthContext);
  return (
    <div>
      {!isAuthenticated ? (
        <h1>Home Page</h1>
      ) : (
        <h1>Home Page, welcome {user.username}</h1>
      )}
    </div>
  );
};

export default Home;
