export default {
  getTodos: () => {
    return fetch("https://condominiolx.herokuapp.com/user/todos").then(
      (response) => {
        //passport envia el error 401 si no se esta logeado
        if (response.status !== 401) {
          return response.json().then((data) => data);
        } else return { message: { msgBody: "UnAuthorized", msgError: true } };
      }
    );
  },
  postTodo: (todo) => {
    return fetch("https://condominiolx.herokuapp.com/user/todo", {
      method: "post",
      body: JSON.stringify(todo),
      headers: {
        "Content-Type": "application/json",
      },
    }).then((response) => {
      if (response.status !== 401) {
        return response.json().then((data) => data);
      } else return { message: { msgBody: "UnAuthorized" }, msgError: true };
    });
  },
  getUsers: () => {
    return fetch("https://condominiolx.herokuapp.com/user/users").then(
      (response) => {
        //passport envia el error 401 si no se esta logeado
        if (response.status !== 401) {
          return response.json().then((data) => data);
        } else return { message: { msgBody: "UnAuthorized", msgError: true } };
      }
    );
  },
  deleteUser: (id) => {
    return fetch(`https://condominiolx.herokuapp.com/user/usersDelete/${id}`, {
      method: "post",
      //body: JSON.stringify(todo),
      headers: {
        "Content-Type": "application/json",
      },
    }).then((response) => {
      if (response.status !== 401) {
        return response.json().then((data) => data);
      } else return { message: { msgBody: "UnAuthorized", msgError: true } };
    });
  },
};
